@mixin hover($property, $toValue) {
  transition: #{$property} $effectDuration;
  &:hover {
    #{$property}: $toValue;
  }
}

.icon {
  height: 24px;
  width: 24px;
  display: block;
  background-size: cover;
  background-position: center;

  @include hover(transform, $IconTransform);

  @include on-tablet {
    height: 32px;
    width: 32px;
  }

  &--phone {
    background-image: url(./images/Icon-Phone-call.svg);

    &:hover  {
      @include on-desktop {
        width: 180px;
        background-image: url(./images/Phone-number.svg);
      }
    }
  }

  &--menu {
    background-image: url(./images/Icon-Burger-menu.svg);

    &:hover {
      background-image: url(./images/Icon-Burger-menu-hover.svg);
    }
  }

  &--close {
    background-image: url(./images/Icon-Close.svg);
  }
}
