.recommended {
  &__products {
    @include page-grid;

    row-gap: 40px;
  }

  &__product {
    grid-column: 1/-1;

    @include on-tablet {
      grid-column: 2/-2;
    }

    @include on-desktop {
      grid-column: span 4;
    }
  }
}

.product {
  &__title,
  &__category,
  &__price {
    color: $main-text-color;
  }
}
