.top-bar {
  padding-block: 24px;
  display: flex;
  justify-content: space-between;
  align-items: center;

  @include on-tablet {
    padding-block: 64px;
  }

  &__icons {
    display: flex;
    gap: 24px;
    position: relative;
  }

  &__logo {
    display: flex;
    height: 16px;
    transition: transform $effectDuration;

    @include on-tablet {
      height: 24px;
    }

    &:hover {
      transform: $FotoTransform;
    }
  }
}
